import styled from "styled-components";
import DropDown from "../atoms/DropDown";

const NavMenu = () => {
  const data = [
    {
      ul: "",
      li: [
        "",
        "",
        " ",
        "",
        "",
        "",
      ],
    },
    {
      ul: "",
      li: [
        " ",
        "",
        "",
        "",
        "  ",
        " ",
        " ",
      ],
    },
    {
      ul: "",
      li: [
        " ",
        "   ",
        "  ",
      ],
    },
    {
      ul: " ",
      li: [
        " ",
        "",
        " ",
        " ",
        " ",
        " ",
        " ",
        " ",
      ],
    },
    { ul: " ", li: false },
  ];
  return (
    <Container>
      {data.map((data) => (
        <Menu key={data.ul}>
          {data.ul}
          <DropDown item={data.li}></DropDown>
        </Menu>
      ))}
    </Container>
  );
};

export default NavMenu;

const Container = styled.div`
  display: flex;
  font-size: 24px;
`;
const Menu = styled.div`
  position: relative;
  padding: 0 25px;
  :last-child {
    padding-right: 0pc;
  }
`;
