export default Animalplay;

const Container = styled.div``;

const Reulbox = styled.div`
  padding: 20px;
  h1 {
    font-size: 24px;
    color: #333;
    font-weight: bold;
  }
  @media screen and (max-width: 800px) {
    h1 {
      text-align: center;
    }
    padding: 0px;
  }
`;

const Reul = styled.div`
  display: flex;
  align-items: center;

  img {
    padding-left: 20px;
    height: 300px;
  }
  ul {
    padding-left: 20px;
    width: 100%;
  }
  li {
    margin-top: 10px;
    font-size: 18px;
    color: #555;
    list-style-type: disc;
  }
  li:last-child {
    list-style-type: none;
    color: red;
  }
  strong {
    font-weight: 700;
  }

  @media screen and (max-width: 800px) {
    flex-direction: column-reverse;
    img {
      width: 100%;
      padding-left: 0px;
      margin-top: 30px;
      height: 450px;
    }
  }
`;

const Mapwrapper = styled.div`
  h1 {
    font-size: 17px;
    color: #333;
    font-weight: bold;
    margin: 10px 0px;
  }
  p {
    font-size: 17px;
    color: #555;
    margin-top: 10px;
  }
  padding: 30px;
  background-color: #f5f5f5;
  overflow: hidden;
  margin-top: 30px;
  table {
    border-top: 3px solid #594940;
    width: 100%;
    margin-top: 20px;
  }

  th {
    padding: 21px 10px 21px;
    text-align: center;
    background-color: #f8f8f8;
    border-bottom: 1px solid #d4d4d4;
    font-size: 18px;
    font-weight: bold;
    color: #333;
  }
  td {
    padding: 21px 10px 21px;
    background-color: #ffffff;
    border-bottom: 1px solid #d4d4d4;
    font-size: 17px;
    color: #333;
    text-align: center;
    word-break: keep-all;
  }
  button {
    background-color: #009d8f;
    border-radius: 5px;
    padding: 10px 21px;
    color: #fff;
    font-size: 16px;
    border-color: transparent;
  }
  strong {
    font-weight: 700;
  }
  @media screen and (max-width: 700px) {
    th {
      display: none;
    }
    td {
      border: none;
      padding: 5px 10px;
      text-align: left;
    }
    tr {
      display: flex;
      border-bottom: 1px solid black;
      flex-direction: column;
    }
  }
`;
