import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  activeindex: -1,
  accordionData: [
    {
      id: 0,
      title: " ",
      content: `  120     !

 

           .
      
       
      
-             .
      
-         3    3    .
      
-          ,       7  .
      
-   10     ,           .
      
       
      
  120       .`,
    },
    {
      id: 1,
      title: "   ? ",
      content: `     .

             ,
      
         .
      
       
      
    
      
120       .`,
    },
    {
      id: 2,
      title: "   ",
      content: `    
 Tel. 02)2.1.2.4.-2.8.3.9.
 Tel. 02)2.6.3.6.-7.6.4.5.
      

  ()
 09:00 ~ 18:00
 10:00 ~ 18:00
*      .*`,
    },
  ],
};

const questionSlice = createSlice({
  name: "questionSlice",
  initialState,
  reducers: {
    change: (state, action) => {
      if (state.activeindex === action.payload) {
        state.activeindex = -1;
      } else {
        state.activeindex = action.payload;
      }
    },
  },
});

export const questionSliceAction = questionSlice.actions;
export default questionSlice.reducer;
